@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;700&display=swap");

.container {
    font-family: "Montserrat", sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.box {
    box-shadow: -30px -1rem 10px rgba(24, 251, 35, 0.6),
        20px 0.8rem 20px rgb(226, 43, 55);
    width: 489px;
    height: 521px;
    border-radius: 30px;
    text-align: center;
}

h1 {
    margin-top: 70px;
    font-size: 36px;
    font-weight: 700;
    line-height: 43.88px;
}

.input {
    margin: 30px;
}

input {
    border: 2px solid black;
    padding: 2px 8px;
    width: 300px;
    height: 50px;
    font-size: 30px;
    font-weight: bold;
}

button {
    margin: 5px 10px;
    width: 61px;
    height: 61px;
    background-color: black;
    color: white;
    font-size: 30px;
}

.result {
    color: black;
    font-size: 20px;
    font-weight: bold;
}

.success {
    color: blue;
    font-size: 20px;
    font-weight: bold;
}

.error {
    color: red;
    font-size: 20px;
    font-weight: bold;
}